import { getGenres } from './api';
import { getPopularMoviesWeek } from './api';

let page = 1;
let currentMovieIndex = 0; // Index aktualnie wywietlanego filmu
let moviesList = []; // Lista przechowujca pobrane filmy
let genres = []; // Lista przechowujca pobrane gatunki
const moviesContainer = document.getElementById('movies');
const loadMoreButton = document.getElementById('load-more');

// Funkcja do pobierania liczby filmw na podstawie szerokoci ekranu
function getMoviesPerLoad() {
  const screenWidth = window.innerWidth;
  if (screenWidth < 768) {
    return 1; // Wywietl 1 film na urzdzeniach mobilnych
  } else {
    return 3; // Wywietl 3 filmy na tabletach i laptopach
  }
}

async function getMovies(page = 1) {
  const fetchGenres = await getGenres();
  genres = fetchGenres;

  const fetchData = await getPopularMoviesWeek(page);
  moviesList = moviesList.concat(fetchData.results); // Dodaj filmy do listy
  displayMovies(); // Wywietl filmy z nowo pobranych
}

// Funkcja do dopasowania gatunkw do filmw
export function getGenreNames(genreIds) {
  return genreIds
    .map(id => {
      const genre = genres.find(g => g.id === id);
      return genre ? genre.name : 'Nieznany gatunek';
    })
    .slice(0, 2) // Ogranicz do dwch gatunkw
    .join(', ');
}

function getStars(vote) {
  const fullStars = Math.floor(vote / 2);
  let stars = '';
  for (let i = 0; i < 5; i++) {
    if (i < fullStars) {
      stars += '&#9733;'; // Pena gwiazdka
    } else {
      stars += '&#9734;'; // Pusta gwiazdka
    }
  }
  return stars;
}

// Funkcja do wywietlania filmw
function displayMovies() {
  const moviesPerLoad = getMoviesPerLoad(); // Pobierz liczb filmw do zaadowania

  for (let i = 0; i < moviesPerLoad; i++) {
    if (currentMovieIndex < moviesList.length) {
      const movie = moviesList[currentMovieIndex]; // Pobierz aktualny film
      const genreNames = getGenreNames(movie.genre_ids); // Pobierz nazwy gatunkw dla filmu

      const movieCard = document.createElement('div');
      movieCard.classList.add('movie-card');

      movieCard.innerHTML = `
        <img src="https://image.tmdb.org/t/p/w500${movie.poster_path}" alt="${
        movie.title
      }">
        <div class="movie-info">
          <h2 class="movie-title">${movie.title}</h2> 
          <p>${genreNames} | ${movie.release_date.split('-')[0]}</p>
          <div class="stars">${getStars(movie.vote_average)}</div>
        </div>
      `;

      moviesContainer.appendChild(movieCard);
      currentMovieIndex++; // Zwiksz indeks filmu
    } else {
      // Jeli filmy si skocz, zaaduj kolejn stron
      page++;
      getMovies(page);
      break; // Zakocz ptl, aby czeka na zaadowanie nowych filmw
    }
  }
}

// Obsuga przycisku "Zaaduj wicej"
loadMoreButton.addEventListener('click', () => {
  page++;
  getMovies(page);
});

// Najpierw pobierz list gatunkw, a potem filmy
getMovies(page);

let moviesLoaded = false;

function loadMoreMovies() {
  if (!moviesLoaded) {
    // Symulacja adowania filmw (w rzeczywistoci tu moe by API)
    const newMovies = [
      { title: 'Movie 4', year: 2023 },
      { title: 'Movie 5', year: 2023 },
      { title: 'Movie 6', year: 2023 },
    ];

    const movieList = document.getElementById('movies');

    newMovies.forEach(movie => {
      const movieCard = document.createElement('div');
      movieCard.classList.add('movie-card');
      movieCard.innerHTML = `
        <h3>${movie.title}</h3>
        <p>${movie.year}</p>
      `;
      movieList.insertBefore(movieCard, document.getElementById('load more')); // Wstaw przed przyciskiem
    });

    // Zaznacz, e zaadowano filmy, aby zapobiec wielokrotnemu adowaniu
    moviesLoaded = true;

    // Moesz ukry przycisk See All, jeli nie chcesz go pokazywa po zaadowaniu
    document.getElementById('load more').style.display = 'none';
  }
}
